import React from "react";
import { workProjects } from "./Arrays";
//import { Link } from "react-router-dom";

function Work() {
    return (
        <div className="work-section">
            <h1>Past Work</h1>
            <ol>
                {workProjects.map((projects, id)=> {
                    return (
                        <li key={id}>
                            <h2>{id+1}. {projects.heading}</h2>
                            {projects.img.map((image, index)=> {
                                return (
                                    <img key={index} src={image} alt="design screenshot" />
                                )
                            })}
                        </li>
                    )
                })}
            </ol>
        </div>
    )
}

export default Work;